.block-27 ul li:hover a,
.block-27 ul li:hover span {
  background: #1fc1a9;
  color: #fff;
  border: 1px solid transparent
}

.wrapper {
  width: 100%
}

.wrapper .social-media h3,
.wrapper .social-media .h3 {
  font-size: 20px;
  font-weight: 500
}

.wrapper .social-media p a {
  color: #1fc1a9;
  text-transform: uppercase;
  font-size: 13px;
  font-weight: 500;
  margin-right: 10px
}

.wrapper .social-media p a:hover {
  color: #000
}

.contact-wrap {
  background: #fff
}

.contact-wrap h3,
.contact-wrap .h3 {
  color: #000;
  font-weight: 400;
  font-size: 30px
}

.dbox {
  width: 100%;
  margin-bottom: 25px
}

@media (min-width:768px) {
  .dbox {
    margin-bottom: 0
  }
}

.dbox p {
  margin-bottom: 0
}

.dbox p span {
  font-weight: 400;
  color: #052c43;
  display: block;
  text-transform: uppercase;
  font-size: 13px;
  font-weight: 700
}

.dbox p a {
  color: #1fc1a9
}

.dbox .text {
  width: 100%
}

.contactForm .label {
  color: #1fc1a9;
  text-transform: uppercase;
  font-size: 12px;
  font-weight: 600
}

.contactForm .form-group {
  width: 100%;
  margin-bottom: 10px
}

.contactForm .form-control {
  border: 1px solid rgba(0, 0, 0, .1)
}

.block-21 .blog-img {
  display: block;
  height: 80px;
  width: 80px
}

.block-21 .text {
  width: calc(100% - 90px)
}

.block-21 .text .heading {
  font-size: 18px;
  font-weight: 400;
  margin-bottom: 0
}

.block-21 .text .heading a {
  color: #000
}

.block-21 .text .heading a:hover,
.block-21 .text .heading a:active,
.block-21 .text .heading a:focus {
  color: #1fc1a9
}

.block-21 .text .meta>div {
  display: inline-block;
  font-size: 12px;
  margin-right: 5px
}

.block-21 .text .meta>div a {
  color: gray;
  color: #1fc1a9
}

.tagcloud a {
  text-transform: uppercase;
  display: inline-block;
  padding: 4px 10px;
  margin-bottom: 7px;
  margin-right: 4px;
  border-radius: 4px;
  color: #052c43;
  font-weight: 500;
  border: 1px solid #ccc;
  font-size: 12px;
  background: #fff
}

.comment-form-wrap {
  clear: both
}

.comment-form-wrap .comment-form {
  background: #f9faff
}

.comment-form-wrap .form-group {
  width: 100%;
  margin-bottom: 10px
}

.comment-form-wrap .form-group label {
  font-size: 13px;
  text-transform: uppercase;
  color: #052c43;
  font-weight: 500;
  margin-bottom: 10px
}

.comment-form-wrap .form-control {
  height: 45px;
  background: transparent;
  color: rgba(0, 0, 0, .8);
  font-size: 14px;
  border-radius: 2px;
  -webkit-box-shadow: none!important;
  box-shadow: none!important;
  border: 1px solid rgba(0, 0, 0, .1)
}

.comment-form-wrap .form-control::-webkit-input-placeholder {
  color: rgba(0, 0, 0, .3)!important
}

.comment-form-wrap .form-control::-moz-placeholder {
  color: rgba(0, 0, 0, .3)!important
}

.comment-form-wrap .form-control:-ms-input-placeholder {
  color: rgba(0, 0, 0, .3)!important
}

.comment-form-wrap .form-control:-moz-placeholder {
  color: rgba(0, 0, 0, .3)!important
}

.comment-form-wrap .form-control:focus,
.comment-form-wrap .form-control:active {
  border-color: #1fc1a9!important
}

.comment-form-wrap textarea.form-control {
  height: inherit!important
}

.comment-list {
  padding: 0;
  margin: 0
}

.comment-list .children {
  padding: 50px 0 0 40px;
  margin: 0;
  float: left;
  width: 100%
}

.comment-list li {
  padding: 0;
  margin: 0 0 30px 0;
  float: left;
  width: 100%;
  clear: both;
  list-style: none
}

.comment-list li .vcard {
  width: 80px;
  float: left
}

.comment-list li .vcard img {
  width: 50px;
  border-radius: 50%
}

.comment-list li .comment-body {
  float: right;
  width: calc(100% - 80px)
}

.comment-list li .comment-body h3,
.comment-list li .comment-body .h3 {
  font-size: 18px;
  font-weight: 500
}

.comment-list li .comment-body .meta {
  text-transform: uppercase;
  font-size: 12px;
  letter-spacing: .1em;
  color: ligten(#052c43, 30%);
  font-weight: 500;
  display: block;
  margin-bottom: 10px
}

.comment-list li .comment-body .reply {
  padding: 5px 10px;
  background: #e6e6e6;
  color: #fff;
  text-transform: uppercase;
  font-size: 11px;
  letter-spacing: .1em;
  font-weight: 600;
  border-radius: 4px;
  background: #5579fa
}

.comment-list li .comment-body .reply:hover {
  color: #fff;
  background: #000
}

.search-form .form-group {
  position: relative
}

.search-form .form-group input {
  padding-right: 50px;
  font-size: 14px;
  border: none
}

.search-form .icon {
  position: absolute;
  top: 50%;
  right: 20px;
  -webkit-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
  color: #1fc1a9
}

.sidebar-box {
  margin-bottom: 30px;
  padding: 10px;
  font-size: 15px;
  width: 100%
}

.sidebar-box *:last-child {
  margin-bottom: 0
}

.sidebar-box h3,
.sidebar-box .h3 {
  font-size: 20px;
  margin-bottom: 30px;
  font-weight: 600
}

.categories {
  padding: 0
}

.categories li {
  position: relative;
  margin-bottom: 10px;
  padding-bottom: 10px;
  list-style: none;
  font-size: 16px;
  font-weight: 400
}

.categories li:last-child {
  margin-bottom: 0;
  border-bottom: none;
  padding-bottom: 0
}

.categories li a {
  display: block;
  color: #666;
  padding-left: 25px
}

.categories li a span {
  position: absolute;
  left: 0;
  top: 0;
  color: #1fc1a9
}

.categories li.active a {
  color: #000;
  font-style: italic
}

